@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
.loginForm{
    margin-left: .5em;
    margin-top: 1em;
}

.loginForm .myButton{
    margin-left: 1em;
}
.extraCost{
    font-size: .65em;
    font-weight: 600;
    vertical-align: top;
}
.preview{
    display: flex;
    position: relative;
    width: 35%;
}
.preview .pate{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
}
.preview img{
    position: absolute;
    top:0;
    left:0;
    z-index: 1;
    width: 100%;
}
.preview .basilic{
    z-index: 0;
}
.preview .sauce{
    z-index: 0;
}
.preview .fromage{
    z-index: 0;
}
.pizzaMaker{
    display: flex;
    flex-wrap: wrap;
}

.pizzaMaker h2{
    width: 100%;
}

.pizzaMaker .preview{
    width: 35%;
    margin-left: 5%;
}

.formulaire{
    width: 50%;
    margin-left: 10%;
}

.ChoixGarnitures{
    /* width: 50%; */
    display: flex;
    flex-wrap: wrap;
    grid-gap : 1em;
    gap : 1em;
    margin: 1em 0;
}

.ChoixGarnitures .choix{
    min-width: 20%;
}

.formBtns{
    display: block;
    margin-top: 1em;
}

.formBtns a:last-of-type {
    margin-left: 1em;
}
.presets{
    margin-top: 2em;
}
.preset{
    vertical-align: top;
    display: inline-block;
    position: relative;
    min-width: 175px;
    width: 15%;
    margin: 0 .5em 1em;
    border: 2px solid #eee;
}
.preset .nomPizza{
    text-align: center;
    margin: .5em 0;
}
.preset .pate{
    position: relative;
    top: 0;
    width: 90%;
    z-index: 0;
}
.preset img{
    position: absolute;
    left:5%;
    z-index: 1;
    width: 90%;
}
.preset .basilic{
    z-index: 0;
}
.preset .sauce{
    z-index: 0;
}
.preset .fromage{
    z-index: 0;
}
.preset li{
    text-transform: capitalize;
}

.preset .addToCart{
    display: inline-block;
    /* float: right; */
    /* width: 1em;
    height: 1em; */
}

.pizzas-container{
    display: flex;
}

.pizzaInfo{
    min-width: 35%;
    width: 35vw;
    border-left: 2px solid #eee;
    padding-left: 2em;
    padding-top: 1em;
    height: 85vh;
}

.pizzaInfo .garnituresPreset{
    margin-top: .5em;
    margin-left: 1.2em;
    padding-left: 0;
    display: flex;
    flex-wrap: wrap;
}

.pizzaInfo .garnituresPreset li{
    width: 15em;
}

.price{
    margin: .5em 0 .2em;
}

.hiddenInfo{
    display: none;
}

.pizzaInfo h2{
    margin-left: .7em;
}

.pizzaInfo .preview{
    width: 60%;
    max-width: 50vh;
}

.pizzaInfo .preview .pate{
    position: relative;
}
body{
    margin: 0;
}

:root {
    --darkBlue : rgb(39, 48, 67);
    --lightBlue : rgb(145, 151, 174);
    --whiteish : rgb(239, 246, 238);
    --lightRed : rgb(240, 45, 58);
    --darkRed : rgb(221, 4, 38);
    --activeRed : rgb(241, 80, 104);
    --disabledBtn : rgb(207, 209, 218);
    --disabledBtnText : rgb(93, 93, 93);
}

.App *{
    font-family: 'Quicksand',sans-serif;
    text-decoration: none;
}

main{
    border-top: 2px solid #eee;
}

.myButton, input[type="button"]{
    padding: .7em 1em;
    border-radius: 1px;
    border: 1px solid rgb(221, 4, 38);
    border: 1px solid var(--darkRed);
    background-color: rgb(240, 45, 58);
    background-color: var(--lightRed);
    color: white;
    cursor: pointer;
    text-shadow:0px 1px 0px rgb(221, 4, 38);
    text-shadow:0px 1px 0px var(--darkRed);
    font-weight: bold;
    font-size: .8em;
    transition: ease-in-out .2s;
}

.myButton:hover, input[type="button"]:hover{
    background-color: rgb(221, 4, 38);
    background-color: var(--darkRed);
}

.myButton:active{
    background-color: rgb(241, 80, 104);
    background-color: var(--activeRed);
    box-shadow : 0px 0px 1px 3px rgba(221,4,38,0.28);
}

.myButton:disabled, input[type="button"]:disabled{
    cursor: default;
    background-color: rgb(207, 209, 218);
    background-color: var(--disabledBtn);
    color: rgb(93, 93, 93);
    color: var(--disabledBtnText);
    text-shadow: none;
    border: 1px solid rgb(207, 209, 218);
    border: 1px solid var(--disabledBtn);
}

input[type="text"]{
    padding: .5rem;
    border: 1px solid rgb(145, 151, 174);
    border: 1px solid var(--lightBlue);
    border-radius: 1px;
    transition: ease-in-out .2s;
}

input[type="text"]:focus{
    box-shadow : 0px 0px 1px 3px rgba(221,4,38,0.28);
}

input[type="text"]:focus-visible{
    outline: none;
}

.ChoixGarnitures label{
    text-transform: capitalize;
}

.garnituresPreset .ulTitle{
    margin-left: .2em;
    margin-top: 1em;
}
.garnituresPreset ul{
    margin-top: .5em;
}


.section-head{
    margin-bottom: 2em;
}

.section-head *{
    display: inline;
}

.section-head h2{
    margin-right: 1em;
}
#AppHeader{
    background-color: rgb(239, 246, 238);
    padding: 1em 1em;
    display: flex;
}

h1{
    display: flex;
    flex-grow: 2;
    margin : 0;
}

#mainNav{
    display: flex;
    grid-gap: 1em;
    gap: 1em;
    justify-content: space-between;
    right: 0;
}

